import React,{Component} from 'react';
import {connect} from 'react-redux';
import {
    Show, //展示区容器（除去功能区）
    SearchInfoWrapper,//展示区的一部分；（提供搜索功能）；下拉框区域的的容器，包含多个下拉框和按钮
    ShowWapper,      //展示区的图表，表格等展示区，形象展示；
} from '../style';

import  'echarts/lib/chart/bar';
import  'echarts/lib/chart/line';
import  'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import {actionCreators} from '../store';

import 'bootstrap/dist/css/bootstrap.min.css';
import { 
        ButtonGroup,
        Button,
        Table,
        Row,
        Col,     
} from 'react-bootstrap';

import $ from 'jquery';

class Terms extends Component{
    setTermsTable(){
        const {terms_page,terms_totalPage,terms_mac,getPreTermsMac,getNextTermsMac}=this.props;
        const table=[];
        let i=(terms_page-1)*10;
        for(;i<terms_page*10;i++){
            table.push(
                    <tr key={i}>
                        <td>{i+1}</td>
                        <td>{terms_mac[i]}</td>
                    </tr>
            )
        }
        table.push(
            <tr key={i+1}>
                <td colSpan='2'>
                    <ButtonGroup className="mr-2" aria-label="First group">
                        <Button className='iconfont' size='sm'
                        onClick={()=>{
                            getPreTermsMac(terms_page,terms_totalPage)}}
                        >&#xe610;</Button>
                        <pre>     </pre>
                        <Button className='iconfont' size='sm'
                        onClick={()=>{
                            getNextTermsMac(terms_page,terms_totalPage)}}
                        >&#xe60f;</Button>
                    </ButtonGroup>
                </td>
            </tr>
        )
        return (
            <Table striped bordered hover size="sm" className='showTermsTable mt-4'>
                <thead>
                <tr>
                    <th>No</th>
                    <th>Mac</th>
                </tr>
                </thead>
                <tbody>
                    {table}
                </tbody>
            </Table>
        )
    }
    render(){
        const {terms_personInfo,terms_personMac,getTermsData,getPersonTerms}=this.props;
        return(
            <Show className='showWrapper terms'>
                    <SearchInfoWrapper>
                    <Row className='no-gutters'>
                        <Col xs={4} sm={3} md={2} className='ml-1'>
                        <input placeholder='输入Mac' className='termsInput'
                        onChange={(e)=>{
                            getPersonTerms(e.target.value);
                        }}
                        />
                        </Col>
                        <Col xs={{span:6}} sm={{span:6}}>
                            <Button variant="info" className='btn' size='sm'
                                onClick={() => {
                                    getTermsData();
                                    $('.showTermsTable').show(); 
                                    $('.showTermsResult').hide();
                                }}>已有Mac</Button>
                            <Button variant="info" className='btn' size='sm'
                                onClick={()=>{
                                    $('.showTermsTable').hide(); 
                                    $('.showTermsResult').show();
                                    $('.termsInput')[0].value='';
                            }}>查询</Button>
                        </Col>
                    </Row>
                    </SearchInfoWrapper>
                    <ShowWapper>
                        <Row className='no-gutters'>
                            <Col md={{span:8,offset:2}} lg={{span:6,offset:3}} >
                            {this.setTermsTable()}
                            <Table striped bordered hover size="sm" className='showTermsResult'>
                                <tbody>
                                    <tr>
                                        <th>Mac</th>
                                        <th>来访周期</th>
                                    </tr>
                                    <tr>
                                        <td>{terms_personMac}</td>
                                        <td>{terms_personInfo}</td>
                                    </tr>
                                </tbody>
                            </Table>
                            </Col>
                        </Row>
                    </ShowWapper> 
                </Show>
        )
    }
}
const mapState=(state)=>({
    terms_mac:state.getIn(['search','terms_mac']),
    terms_page:state.getIn(['search','terms_page']),
    terms_totalPage:state.getIn(['search','terms_totalPage']),
    terms_personInfo:state.getIn(['search','terms_personInfo']),
    terms_personMac:state.getIn(['search','terms_personMac']),

});
const mapDispath=(dispatch)=>({
    getTermsData(){
        dispatch(actionCreators.getTermsMac());
    },
    getPersonTerms(mac){
        dispatch(actionCreators.getPersonTerms(mac));
    },
    getPreTermsMac(terms_page,terms_totalPage){
        if(terms_page===1){
            dispatch(actionCreators.changeTermsPage(terms_totalPage));
        }else{
            dispatch(actionCreators.changeTermsPage(terms_page - 1));
        } 
    },
    getNextTermsMac(terms_page,terms_totalPage){
        if(terms_page<terms_totalPage){
            dispatch(actionCreators.changeTermsPage(terms_page + 1));
        }else{
            dispatch(actionCreators.changeTermsPage(1));
        }   
    },
});
export default connect(mapState,mapDispath)(Terms);