import * as constants from './constants';
import axios from 'axios';

const getInfo=(data)=>({
    type:constants.GET_INFO,
    xAxis_data:data.xAxis,
    xAxis_day_data:data.xAxis_day,
    series_data:data.series_data,
    series_days:data.days
});
export const  pageNotFound=()=>({
    type:constants.PAGE_NOT_FOUND,
});
const getActiveInfo=(data)=>({
    type:constants.GET_ATCIVE_INFO,
    active_Mac:data.active_Mac
});
const getFamiliarInfo=(data)=>({
    type:constants.GET_FAMILIAR_INFO,
    familiar_totalPerson:data.totalPerson,
    familiar_familiarPerson:data.familiarPerson,
});

const getTermsInfo=(data)=>({
    type:constants.GET_TERMS_INFO,
    terms_mac:data,
    terms_totalPage:Math.ceil(data.length/10)
});
const getPersonTermsInfo=(data)=>({
    type:constants.GET_PERSON_INFO,
    terms:data.terms,
    mac:data.mac
});
export const getSearchInfo=(year,week)=>{
    return (dispatch)=>{
            axios.get('/api/flow',{year:year,week:week}).then((res)=>{
            const result=res.data;
            const action=getInfo(result.data);
            dispatch(action);
            }).catch(()=>{
            alert('亲，您访问的数据不存在，请重新选择');
            dispatch(pageNotFound());
        })
    }
};
export const getActiveSearchInfo=(year,week)=>{
    return (dispatch)=>{
            axios.get('/api/active',{year:year,week:week}).then((res)=>{
            const result=res.data;
            const action=getActiveInfo(result.data);
            dispatch(action);
            }).catch(()=>{
            alert('亲，您访问的数据不存在，请重新选择');
            dispatch(pageNotFound());
        })
    }
};
export const getFamiliarSearchInfo=(year,week)=>{
    return (dispatch)=>{
            // axios.get('/api/weekData_'+month+'_'+week+'.json')
            axios.get('/api/familiar',{year:year,week:week}).then((res)=>{
            const result=res.data;
            const action=getFamiliarInfo(result.data);
            dispatch(action);
            }).catch(()=>{
            alert('亲，您访问的数据不存在，请重新选择');
            dispatch(pageNotFound());
        })
    }
};

export const getTermsMac=()=>{
    return (dispatch)=>{
            axios.get('/api/terms').then((res)=>{
            const action=getTermsInfo(res.data.data);
            dispatch(action);
            }).catch(()=>{
            dispatch(pageNotFound());
        })
    }
};

export const changeTermsPage=(termsPage)=>({
    type:constants.CHANGE_TERMS_PAGE,
    termsPage
});

export const getPersonTerms=(mac)=>{
    return (dispatch)=>{
        axios.get('/api/termsPerson',mac).then((res)=>{
        const action=getPersonTermsInfo(res.data.data);
       
        dispatch(action);
        }).catch(()=>{
        alert('亲，您访问的数据不存在，请重新选择');
        dispatch(pageNotFound());
    })
    }
};
