import React ,{Component} from 'react';
import {Link} from 'react-router-dom';
import {  HeaderWrapper,Logo,Title,} from './style';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button,Row,Col} from 'react-bootstrap';
import {connect} from 'react-redux';
import {actionCreators} from './store';
import $ from 'jquery';

class Header extends Component {
  componentDidMount(){
    if(window.innerWidth<768){
      $('.title').hide();
    }
  }
  render(){
    const {login,handleBtnClick}=this.props;
    return (
      <HeaderWrapper>
      <Row className='no-gutters'>
          <Col xs={2} md={2}  className='ml-3' >
             <Logo/>
          </Col>
          <Col  xs={6} md={8}>
              <Title className='title'>WIFI探针项目</Title>
          </Col>
          <Col xs={3} md={1} className='mt-2 ml-md-3 ml-lg-5'>
          {   !login?
                <Link to='/login'>
                    <Button variant="info">登录</Button>
                </Link>
              :
              <Button variant="secondary" className='ml-1'
              onClick={()=>{handleBtnClick();}}
              >注销</Button> 

          }
          </Col>
      </Row>
      </HeaderWrapper>
    );
  }
}
const mapState=(state)=>({
  login:state.getIn(['login','status']),
})
const mapDispatch=(dispatch)=>({
    handleBtnClick(){
        dispatch(actionCreators.change_login());
    }
})
export default connect(mapState,mapDispatch)(Header);