import axios from 'axios';
import * as constants from './constants';


const loginStatus=(data)=>({
    type:constants.LOGIN_STATUS,
    status:data.status
});
const signinStatus=(data)=>({
    type:constants.SIGNIN_STATUS,
    status:data.status
});

export const login=(account,password)=>{
    return (dispatch)=>{
        axios.post('/login',{account:account,password:password}).then((res)=>{
            const action=loginStatus(res.data);
            dispatch(action);
        }).catch((err)=>{
            console.log(err);
        })
    }
};

export const signin=(account,password)=>{
    return (dispatch)=>{
        axios.post('/register',{account:account,password:password}).then((res)=>{
            const action=signinStatus(res.data);
            dispatch(action);
        }).catch((err)=>{
            console.log(err);
        })
    }
}