import React,{Component} from 'react';

import {
    Show, //展示区容器（除去功能区）
    SearchInfoWrapper,//展示区的一部分；（提供搜索功能）；下拉框区域的的容器，包含多个下拉框和按钮
    //SearchInfoSelect,//下拉框的容器
    ShowWapper,      //展示区的图表，表格等展示区，形象展示；
   
} from '../style';


class About extends Component{
    render(){
        return(
            <Show className='showWrapper about'>
            <SearchInfoWrapper className='text-center pt-2'><h3>系统简介</h3></SearchInfoWrapper>
            <ShowWapper >
                <div className='project'>
                    <p>
                        WIFI 探针基于WIFI探测技术来识别无线WIFI接入点附近开启WIFI功能的
                        智能手机或者WIFI终端（笔记本，平板电脑等），
                        用户不需要主动接入WIFI，探针就能够识别用户，无论是IOS或者安卓系统都能检测到，
                        通过获取设备的MAC地址。对用户来说探针是透明而不可感知。
                    </p>  
                    <p>
                        本项目通过对于校园内的餐厅进行WIFI探针检测，
                        进而统计出不同时间段的人流量，对于数据进行收集和整理
                        。最终在前端界面进行展示，可提供查询某个地点的人流量，
                        某个用户在某个时间区间的在某个地点出现的次数等一系列统计报表。
                    </p> 
                </div>
                    
            </ShowWapper>
                    
            </Show>
        )
    }
}

export default About;