import React,{Component} from 'react';
import {connect} from 'react-redux';
import {
    ContentWrapper, 
    FuncWrapper, //功能展示区的容器
    WeChat,
    Qq,
} from './style';
//import echarts from 'echarts/lib/echarts';
import  'echarts/lib/chart/bar';
import  'echarts/lib/chart/line';
import  'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
//import {actionCreators} from './store';
import {IconStyle} from '../../statics/iconfont/iconfont';

import 'bootstrap/dist/css/bootstrap.min.css';
import { ListGroup,
        Row,
        Col,     
} from 'react-bootstrap';

//import {Redirect} from 'react-router-dom';
import $ from 'jquery';

import Flow from './component/flow';
import Active from './component/active';
import Terms from './component/terms';
import Familiar from './component/familiar';
import Phone from './component/phone';
import About from './component/about';
import {Redirect} from 'react-router-dom';



class Search extends Component{
    componentDidMount(){
        $('.showWrapper').hide();
        $('#main').hide();
        $('.showActiveTable').hide();
        $('.showTermsTable').hide();
        $('.showTermsResult').hide();
        $('.image').hide();
        $('#wechat').hide();
        $('#qq').hide();
        if(window.innerWidth<768){
            $('.funcTel').hide();
            $('.funcProject').hide();
            $('.funcWrapper').css('height','250px');
        }
    }
    selectShowWrapper(zoom,funcItem){
        $(zoom).siblings().hide();
        $(zoom).show();
        $(funcItem).siblings().css('background-color','#333');
        $(funcItem).css('background-color','#8fbc8f');
        $('.image').hide();
    }
    render(){
    //const {login}=this.props;
    return(
        <ContentWrapper>
            <Row className='no-gutters'>
            <IconStyle/>
            <Col sm={2}>
            <ListGroup>
                <FuncWrapper className='funcWrapper'>
                <ListGroup.Item className='funcItem funcFlow iconfont'
                    onClick={()=>{
                        this.selectShowWrapper('.flow','.funcFlow');
                    }}>&#xe629; &nbsp;人流量</ListGroup.Item>
                <ListGroup.Item className='funcItem funcActive iconfont'
                    onClick={()=>{
                        this.selectShowWrapper('.active','.funcActive');
                    }}>&#xe77f; &nbsp;活跃度</ListGroup.Item>
                <ListGroup.Item className='funcItem funcTerms iconfont'
                    onClick={()=>{
                        this.selectShowWrapper('.terms','.funcTerms');
                    }}>&#xe6c3; &nbsp;来访周期</ListGroup.Item>
                <ListGroup.Item className='funcItem funcFamiliar iconfont'
                    onClick={()=>{
                        this.selectShowWrapper('.familiar','.funcFamiliar');
                    }}>&#xe747; &nbsp;熟客比重</ListGroup.Item>
                <ListGroup.Item className='funcItem funcPhone iconfont'
                    onClick={()=>{
                        this.selectShowWrapper('.phone','.funcPhone');
                    }}>&#xe615; &nbsp;手机品牌</ListGroup.Item>
                <ListGroup.Item className='funcItem funcProject iconfont'
                    onClick={()=>{
                        this.selectShowWrapper('.about','.funcProject');
                    }}>&#xe602; &nbsp;关于项目</ListGroup.Item>
                <ListGroup.Item className='funcItem funcTel iconfont'
                    onClick={()=>{
                        $('.about').siblings().hide();
                        $('.about').hide();
                        $('.funcTel').siblings().css('background-color','#333');
                        $('.funcTel').css('background-color','#8fbc8f');
                        $('.image').show();
                    }}
                    >&#xe61a; &nbsp;联系我们
                    
                </ListGroup.Item>
                <ListGroup.Item className='funcItem image iconfont'>
                    <Row className='no-gutters'>
                        <Col className='wechat' md={3} xs={{span:4,offset:2}} 
                            onMouseEnter={()=>$('#wechat').show()}
                            onMouseLeave={()=>$('#wechat').hide()}
                        >
                            <span className='ml-2 '>&#xe620;</span>
                            <WeChat id='wechat' className=''/>
                             
                        </Col>
                        <Col className='qq' md={3}  xs={{span:4,offset:1}} 
                            onMouseEnter={()=>$('#qq').show()}
                            onMouseLeave={()=>$('#qq').hide()}
                        >
                            <span className='ml-4'>&#xe600;</span>
                            <Qq id='qq' className='mr-xs-5 mr-lg-0'/>
                        </Col>
                    </Row>     
                </ListGroup.Item>
                </FuncWrapper> 
            </ListGroup> 
            </Col>
            <Col sm={10}>
                <Flow/>
                <Active/>
                <Terms/>
                <Familiar/>
                <Phone/>
                <About/>
            </Col> 
        </Row>
        </ContentWrapper>
        )
    }
}
const mapState=(state)=>({
    login:state.getIn(['login','status']),
});
const mapDispatch=(dispatch)=>({
})
export default connect(mapState,mapDispatch)(Search);