import React,{Component} from 'react';
import {connect} from 'react-redux';
import echarts from 'echarts/lib/echarts';
import  'echarts/lib/chart/bar';
import  'echarts/lib/chart/line';
import  'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import {actionCreators} from '../store';
import {
    Show, //展示区容器（除去功能区）
    SearchInfoWrapper,//展示区的一部分；（提供搜索功能）；下拉框区域的的容器，包含多个下拉框和按钮
    ShowWapper,      //展示区的图表，表格等展示区，形象展示；
} from '../style';

import 'bootstrap/dist/css/bootstrap.min.css';
import { 
        Button,
        Row,
        Col,     
} from 'react-bootstrap';

import $ from 'jquery';

import 'antd/dist/antd.css';
import { DatePicker } from 'antd';
const { WeekPicker } = DatePicker;

class Flow extends Component{
    setChartState(xAxis_data,seriesType,seriesData){
        let text='人流量展示',seriesName='人数';
        var myChart = echarts.init(document.getElementById('main'));
        myChart.setOption({
            title: {text:text},
            tooltip: {},
            legend:{},
            xAxis: {
                type: 'category',
                axisLabel: {
                interval:'auto',
                rotate:50,
                },
                data: xAxis_data
            },
            yAxis: {},
            series: [{
                name: seriesName,
                type: seriesType,
                data: seriesData
            }]
        });
    };
    setChart(){
        $('#main').show();
        const {xAxis,xAxis_day,series_data,series_days}=this.props;
         const newxAxis=xAxis.toJS();
         const newseries_data=series_data.toJS();
         const newdays=series_days.toJS();
         const newxAxis_day=xAxis_day.toJS();
         this.setChartState(newxAxis,'bar',newseries_data);
         var myChart = echarts.init(document.getElementById('main'));
        //params.name;
        myChart.on('click', (params) =>{
            if(params.componentType === 'series'){
                if (params.seriesType === 'bar') 
                {
                    switch(params.name){
                        case newxAxis[0]:this.setChartState(newxAxis_day,"line",newdays.day_1);break;
                        case newxAxis[1]:this.setChartState(newxAxis_day,"line",newdays.day_2);break;
                        case newxAxis[2]:this.setChartState(newxAxis_day,"line",newdays.day_3);break;
                        case newxAxis[3]:this.setChartState(newxAxis_day,"line",newdays.day_4);break;
                        case newxAxis[4]:this.setChartState(newxAxis_day,"line",newdays.day_5);break;
                        case newxAxis[5]:this.setChartState(newxAxis_day,"line",newdays.day_6);break;
                        case newxAxis[6]:this.setChartState(newxAxis_day,"line",newdays.day_7);break;
                        default:
                    }
                 }}
            });
    }
    render(){
        const {getFlowData}=this.props;
        return(
            <Show className='showWrapper flow'>                   
                    <SearchInfoWrapper>
                    <Row className='no-gutters'>
                        <Col xs={{span:4}} sm={{span:2}}  className='ml-1'>
                            <WeekPicker className='mt-2 week'  placeholder="按周查询" 
                            onChange={(data,week)=>{
                                var parm=week.split('-');
                                parm[1]=parm[1].replace(/[^0-9]/ig,"");
                                getFlowData(parm[0],parm[1]);
                            }}
                            />
                        </Col>
                        <Col xs={{span:4}} md={{span:2}}>
                            <Button variant="info" className='btn' size='sm' onClick={() => {
                                this.setChart();
                            }}
                            >显示</Button>
                        </Col>
                    </Row>
                    </SearchInfoWrapper>
                    <ShowWapper>
                        <Row className='no-gutters'>
                            <Col sm={12} md={{span:8,offset:2}} lg={{span:6,offset:3}}>
                                    <div id='main' className='showchart'></div>
                            </Col>
                        </Row>
                    </ShowWapper>
            </Show>
        )
    }
}
const mapState=(state)=>({
   
    xAxis:state.getIn(['search','xAxis']),
    xAxis_day:state.getIn(['search','xAxis_day']),
    series_data:state.getIn(['search','series_data']),
    series_days:state.getIn(['search','series_days']),
});
const mapDispath=(dispatch)=>({
    getFlowData(year,week){
        dispatch(actionCreators.getSearchInfo(year,week));
    },
});
export default connect(mapState,mapDispath)(Flow);