import {fromJS} from 'immutable';
import * as constants from './constants';

const  defaultState=fromJS({
    xAxis:[],
    xAxis_day:[],
    series_data: [],
    series_days:{},
    
    active_Mac:[],
    terms_mac:[],
    terms_page:1,
    terms_totalPage:1,
    terms_personInfo:"",
    terms_personMac:"",

    familiar_totalPerson:0,
    familiar_familiarPerson:0,

});

export default (state=defaultState,action)=>{
    switch(action.type){
        case constants.PAGE_NOT_FOUND:
            return state.set('page_not_found',true);
        case constants.GET_INFO:
            return state.merge({
                'xAxis':fromJS(action.xAxis_data),
                'xAxis_day':fromJS(action.xAxis_day_data),
                'series_data':fromJS(action.series_data),
                'series_days':fromJS(action.series_days),
                
            });
        case constants.GET_ATCIVE_INFO:
            return state.merge({
                'active_Mac':action.active_Mac,
                
            }); 
        case constants.GET_FAMILIAR_INFO:
            return state.merge({
                'familiar_totalPerson':action.familiar_totalPerson,
                'familiar_familiarPerson':action.familiar_familiarPerson,
               
            });
        case constants.CHANGE_TERMS_PAGE:
            return state.set('terms_page',action.termsPage);
        case constants.GET_PERSON_INFO:
            return state.merge({
                'terms_personInfo':action.terms,
                'terms_personMac':action.mac,
               
            });
        
        case constants.GET_TERMS_INFO:
            return state.merge({
                'terms_mac':action.terms_mac,
                'terms_totalPage':action.terms_totalPage,
                
            }) 
        
        
        default:
            return state;
    }
}