import styled from 'styled-components';

export const FooterWrapper=styled.div`

    width:100%;
    height:80px;
    background:#333;
    color:#969696;
    .group{
        text-align:center;
        font-size:16px;
        cursor:pointer;
    }
    .group:hover{
        color:#fff;
    }
    .address{
        text-align:center;
        font-size:14px;
        cursor:pointer;
    }
    .address:hover{
        color:#fff;
    }
`;