import React ,{Component} from 'react';
import {Redirect} from 'react-router-dom';
import {connect} from 'react-redux';
import {LoginWrapper,Form,ButtonDL,ButtonZC} from './style';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Row,Col,} from 'react-bootstrap';
import {IconStyle} from '../statics/iconfont/iconfont';

import {actionCreators} from './store';

import $ from 'jquery';

class Login extends Component {
  componentDidMount(){
    $('.dl').css('border-bottom','2px solid #ea6f5a');
    $('.zc-btn').hide();
  }
  render(){
    const {status,login,signin_status,signin}=this.props;
    if(!status){
      return (
        <LoginWrapper className='login'>
          <Row className=' no-gutters' > 
              <IconStyle />
              <Col xs={{span:10,offset:1}} sm={{span:5,offset:3}} lg={{span:3,offset:4}} className='mt-5'>
                <Form>
                      <div className='title-wrapper'>
                        <span className='title dl'
                          onClick={()=>{
                            $('.zc-btn').hide();
                            $('.dl-btn').show();
                            $('.dl').css('border-bottom','2px solid #ea6f5a');
                            $('.zc').css('border-bottom','none');
                          }}
                        >登录</span> 
                        <b>·</b>
                        <span className='title zc'
                        onClick={()=>{
                          $('.zc').css('border-bottom','2px solid #ea6f5a');
                          $('.dl').css('border-bottom','none');
                          $('.dl-btn').hide();
                          $('.zc-btn').show();
                        }}
                       >注册</span>
                      </div>
                        <div className='input-wrapper-t iconfont'>
                            &nbsp;&#xe654;
                          <input className='input-t ml-2' placeholder="账号"/>
                        </div>
                        <div className='input-wrapper-b iconfont'>
                          &nbsp;&#xe628;
                          <input className='input-b ml-2' type="password" placeholder="密码" />
                        </div>
                       <input type="checkbox"/>  <span>记住我</span>
                      <ButtonDL className='text-center dl-btn'
                        onClick={()=>{
                          login($('.input-t')[0].value,$('.input-b')[0].value)
                          if(!status) {
                            $('.input-t')[0].value='';
                            $('.input-b')[0].value='';
                          }
                        }}
                      >登录</ButtonDL>
                      <ButtonZC className='text-center zc-btn'
                        onClick={()=>{
                          signin($('.input-t')[0].value,$('.input-b')[0].value)
                          if(!signin_status) {
                            $('.input-t')[0].value='';
                            $('.input-b')[0].value='';
                          }
                        }}
                      >注册</ButtonZC>
                  </Form>
              </Col>
          </Row> 
        </LoginWrapper>
    );
    }else{
        return <Redirect to='/' />
    }
    
  }
}
const mapState=(state)=>({
      status:state.getIn(['login','status']),
      signin_status:state.getIn(['login','status']),
});

const mapDispatch=(dispatch)=>({
  login(account,password)
  {
    dispatch(actionCreators.login(account,password));
  },
  signin(account,password)
  {
    dispatch(actionCreators.signin(account,password));
  },
});

export default connect(mapState,mapDispatch)(Login);