import styled from 'styled-components';
import wechat from '../../statics/wechat.png';
import qq from '../../statics/qq.png';
import page404 from '../../statics/404.png';

export const ContentWrapper=styled.div`
    width:100%;
    // margin: 0 auto;
    height:489px;
    background:#666;
`;
export const Show=styled.div`
    width:100%;
    &.hidden{
        display:none;
    }
    &.show{
        display:block;
    }
`;
export const Page404=styled.div`
    width:100%;
    height:439px;
    background-color:#fff;
    //以下两个属性经常一起使用
    background-repeat:no-repeat;
    background-image:url(${page404});
    //
    background-size:contain;
`;
export const ShowWapper=styled.div`
    width:100%;
    // float:left;
    height:439px;
    background-color:#dcdcdc;
    
    .showActiveTable{
        margin:0 10%;
        height:80%;
        width:80%;
        text-align:center;
        border:1px solid #fff;
    }
    .showTermsTable{
        margin:0 10%;
        height:80%;
        width:80%;
        text-align:center;
        border:1px solid #fff;
    }
    .showTermsResult{
        margin:5% 10% 0 10%;
        height:80%;
        width:80%;
        text-align:center;
        border:1px solid #fff;
    }
    .showchart{
        margin:5% 5%;
        width:90%;
        height:370px;
        overflow:auto;
    }
    .project{
        margin:0 10%;
        padding-top:5%;
        height:80%;
        width:80%;
    }
    
`;

export const FuncWrapper=styled.div`
    float:left;
    color:#fff;
    width:100%;
    height:489px;
    background:#333;
    .funcItem{
        text-align:center;
        background-color:#333;
        padding:auto 15px;
        cursor:pointer;
    }
    .image{
        background:#dcdcdc;
    }
`;

export const SearchInfoWrapper=styled.div`
    width:100%;
    height:49px;
    margin-bottom:1px;
    padding-left:10px;
    background-color:#dcdcdc;
    .btn{
        margin-top:10px;
        margin-left:5px;
    }
    .termsInput{
        margin-top:10px;
        width:95%;
`;
export const SearchInfoSelect=styled.select`
    margin:7px 0;
    width:100%;
    height:34px;
`;
//-----------------about界面-------------------
export const AboutWrapper=styled.div`
    float:right;
    color:#fff;
    width:100%;
    height:240px;
    padding:10% 10%;
    background:#2f4f4f;
    .wechat{
        display:block;
    }
    .qq{
        display:block;
    }
`;
export const WeChat=styled.div`
    height:100px;
    width:100px;
    margin-top:-3px;
    border:2px solid #333;
    background-repeat:no-repeat;
    background-image:url(${wechat});
    background-size:contain;
`;
export const Qq=styled.div`
    height:100px;
    width:90px;
    margin-top:-3px;
    border:2px solid #333;
    background-repeat:no-repeat;
    background-image:url(${qq});
    background-size:contain;
`;
//--------------------------------------------













