import React,{Component} from 'react';
import {connect} from 'react-redux';
import echarts from 'echarts/lib/echarts';
import  'echarts/lib/chart/bar';
import  'echarts/lib/chart/line';
import  'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import {actionCreators} from '../store';
import {
    Show, //展示区容器（除去功能区）
    SearchInfoWrapper,//展示区的一部分；（提供搜索功能）；下拉框区域的的容器，包含多个下拉框和按钮
    ShowWapper,      //展示区的图表，表格等展示区，形象展示；
} from '../style';

import 'bootstrap/dist/css/bootstrap.min.css';
import { 
        Button,
        Row,
        Col,     
} from 'react-bootstrap';
import 'antd/dist/antd.css';
import { DatePicker } from 'antd';
const { WeekPicker } = DatePicker;

class Familiar extends Component{
    
    setPieState(pieName,familiar_familiarPerson,familiar_totalPerson){
        var myPie = echarts.init(document.getElementById(pieName));
        myPie.setOption({
            title:{text:'熟客比重图'},
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
                orient: 'vertical',
                x: 'left',
                data:['熟客','其他访客']
            },
            series: [
                {
                    name:'访问来源',
                    type:'pie',
                    radius: ['50%', '70%'],
                    avoidLabelOverlap: false,
                    label: {
                        normal: {
                            show: false,
                            position: 'center'
                        },
                        emphasis: {
                            show: true,
                            textStyle: {
                                fontSize: '30',
                                fontWeight: 'bold'
                            }
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    data:[
                        {value:familiar_familiarPerson, name:'熟客'},
                        {value:familiar_totalPerson-familiar_familiarPerson, name:'其他访客'},
                    ]
                }
            ]
        });
    }
    render(){
        const {familiar_familiarPerson,familiar_totalPerson,getfamiliarData}=this.props;
        return(
            <Show className='showWrapper familiar'>
                    <SearchInfoWrapper>
                    <Row className='no-gutters'>
                        <Col xs={{span:4}} sm={{span:2}}  className='ml-1'>
                            <WeekPicker className='mt-2 week'  placeholder="按周查询" 
                            onChange={(data,week)=>{
                                var parm=week.split('-');
                                parm[1]=parm[1].replace(/[^0-9]/ig,"");
                                getfamiliarData(parm[0],parm[1]);
                            }}
                            />
                        </Col>
                        <Col sm={{span:4}} xs={{span:5}}>
                            <Button variant="info" className='btn'  size='sm'
                            onClick={() => {
                                this.setPieState('myFamiliarPie',familiar_familiarPerson,familiar_totalPerson)}}
                                >显示</Button>
                        </Col>
                    </Row>
                    </SearchInfoWrapper>
                        <ShowWapper>
                        <Row className='no-gutters'>
                            <Col sm={12} md={{span:8,offset:2}} lg={{span:6,offset:3}}>
                                <div id='myFamiliarPie' className='showchart'></div>
                            </Col>
                        </Row>
                    </ShowWapper>
                </Show>
        )
    }
}
const mapState=(state)=>({

    familiar_totalPerson:state.getIn(['search','familiar_totalPerson']),
    familiar_familiarPerson:state.getIn(['search','familiar_familiarPerson']),
});
const mapDispath=(dispatch)=>({
    getfamiliarData(year,week){
        dispatch(actionCreators.getFamiliarSearchInfo(year,week));
    },
});
export default connect(mapState,mapDispath)(Familiar);