import styled from 'styled-components';
import logo from '../statics/logo.png';

export const LoginWrapper=styled.div`
    height:625px;
    width:100%;
    display:bolck;
    background:#333;
`;
export const Logo=styled.div`
    height:56px;
    width:56px;
    border-radius:28px;
    display:bolck;
    //以下两个属性经常一起使用
    background-repeat:no-repeat;
    background-image:url(${logo});
    //
    background-size:contain;
`;
export const Form=styled.div`
    margin-top:15%;
    width:100%;
    height:300px;
    background:#fff;
    padding:0 10%;
    color:#969696;
    box-shadow: 0 0 8px rgba(0,0,0,.1);
    .input-wrapper-t{
        width:100%;
        height:50px;
        font-size: inherit;
        line-height: inherit;
        background: hsla(0,0%,71%,.1);
        border: 1px solid #c8c8c8;
        border-radius: 4px 4px 0 0;
    }
    .input-wrapper-b{
        width:100%;
        height:50px;
        font-size: inherit;
        line-height: inherit;
        background: hsla(0,0%,71%,.1);
        border: 1px solid #c8c8c8;
        border-radius: 0 0 4px 4px;
        margin-bottom:10px;
    }
    .input-t{
        width:85%;
        height:40px;
        margin-top:5px;
        border:none;
        font-size: inherit;

        //hsla 
        //H（色调：Hue）：衍生于色盘，其中0和360是红色，接近120的是绿色，240是蓝色；
        //S（饱和度：Saturation）：值为一个百分比数，0%代表灰度，100%代表最高饱和度；
        //L（亮度：Lightness）：值也为一个百分比数，其中0%代表最暗，50%为均值，100%表示最亮。
        //A（透明度：Alpha）：值为0~1之间的一个数，其中0代表不透明，1代表完全透明。
        background: hsla(0,0%,100%,.1);

        //去除input获得焦点的边框
        outline:none;
    }
    .input-b{
        width:85%;
        height:40px;
        margin-top:5px;
        border:none;
        font-size: inherit;
        background: hsla(0,0%,100%,.1);
        outline:none;
    }
    .title-wrapper{
        width:80%;
        height:70px;
        margin:0 10%;
        padding-top:10px;
        text-align:center;
    }
    .title{
        margin:0 10px;
        font-size:24px;
        width:50px;
        padding-bottom:10px;
        color: #ea6f5a;
        //border-bottom:2px solid #ea6f5a;
    }
`;
export const ButtonDL=styled.div`
    background: #3194d0;
    margin-top: 20px;
    width: 100%;
    padding: 9px 18px;
    font-size: 18px;
    border: none;
    border-radius: 25px;
    color: #fff;
    cursor: pointer;
    outline: none;
    display: block;
    clear: both;
`;
export const ButtonZC=styled.div`
    background: #42c02e;
    margin-top: 20px;
    width: 100%;
    padding: 9px 18px;
    font-size: 18px;
    border: none;
    border-radius: 25px;
    color: #fff;
    cursor: pointer;
    outline: none;
    display: block;
    clear: both;
`;